<template>
  <div class="show_content">
    <div v-for="(n,index) in ex" :key="index">
      {{index+1}}. {{n.name}}
      <v-btn :class="favs[index].fav!=0 ? 'red--text' : ''" icon @click="likeFav(index)">
        <v-icon :dense="true">mdi-heart</v-icon>
      </v-btn>
      <img class=".ex1" :src="n.foodImg" alt="" />
      <!--span v-if="n.count!=''">喜愛數:{{n.count}}</span>
      <span v-else>{{info(index)}}</span-->
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      ex: [
        { name: "測試圖1", count: "", foodImg:'/img/food5.2f0df1bf.png' },
        { name: "測試圖2", count: "", foodImg:'/img/food2.326896db.png' },
        { name: "測試圖3", count: "", foodImg:'/img/food6.087c3315.png' },
        { name: "測試圖4", count: "", foodImg:'/img/food3.22a269bd.png' },
        { name: "測試圖5", count: "", foodImg:'/img/food4.a64d832d.png' },
        { name: "測試圖6", count: "", foodImg:'/img/food1.d6c4171b.png' },
      ],
      favs: this.$store.state.menuinfo.favs,
    };
  },
  methods: {
    likeFav(index) {
      //計算喜歡~因不是存取數據庫刷新歸0,無法紀錄
      this.$store.commit("menuinfo/set_fav", index);

      if (this.favs[index].fav == 1) {
        this.updata(index, "+");
      } else {
        this.updata(index, "-");
      }
    },
    updata(index, sign) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: sign })
        )
        .then((response) => {
          this.ex[index].count = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    info(index) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: "*" })
        )
        .then((response) => {
          this.ex[index].count = response.data;
          //this.ex=response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style>
.ex1 {
  width: auto;
  height: auto;
  display: block;
  margin: auto;
}
</style>